import React from "react";
import Grid from "@mui/material/Grid";
import MiniStatisticsCard from "components/Cards/StatisticsCards/MiniStatisticsCard";
import VuiBox from "components/VUI/VuiBox";

// React icons
import { AiFillGift } from "react-icons/ai";
import { IoGlobe } from "react-icons/io5";
import { AiOutlineGift } from "react-icons/ai";
import { IoTimerSharp } from "react-icons/io5";


function AnalyticBoard(props) {

    const { totalRewards, referralNum, secondRefNum, deadline } = props;

    React.useLayoutEffect(() => {
    }, []);

    return (
        <>
            <VuiBox mt={3}>
                <Grid container spacing={3}>
                    <Grid item xs={12} md={6} xl={3}>
                    <MiniStatisticsCard
                        title={{ text: "Total Rewards", fontWeight: "regular" }}
                        count={"$"+totalRewards}
                        icon={{ color: "info", component: <IoGlobe size="22px" color="white" /> }}
                    />
                    </Grid>
                    <Grid item xs={12} md={6} xl={3}>
                    <MiniStatisticsCard
                        title={{ text: "Referral Count" }}
                        count={referralNum}
                        icon={{ color: "info", component: <AiFillGift size="22px" color="white" /> }}
                    />
                    </Grid>
                    <Grid item xs={12} md={6} xl={3}>
                    <MiniStatisticsCard
                        title={{ text: "Second Ref Count" }}
                        count={secondRefNum}
                        icon={{ color: "info", component: <AiOutlineGift size="22px" color="white" /> }}
                    />
                    </Grid>
                    <Grid item xs={12} md={6} xl={3}>
                    <MiniStatisticsCard
                        title={{ text: "Referral Deadline" }}
                        count={deadline}
                        icon={{ color: "info", component: <IoTimerSharp size="20px" color="white" /> }}
                    />
                    </Grid>
                </Grid>
            </VuiBox>
        </>
    );
}
export default AnalyticBoard;