import Web3 from 'web3';
import { rpcUrl, contractAddress, tokenAddress } from "../constants/config";
import { nftABI } from '../constants/nftABI';
import { tokenABI } from 'constants/tokenABI';

const web3 = new Web3(new Web3.providers.HttpProvider(rpcUrl));

const getContract = (abiData, address) => {
    const abi = abiData;
    let contract = new web3.eth.Contract(abi , address);
    return contract;
}

const splitString = (str) => {
    return str.split("#");
}

const contract = getContract(nftABI, contractAddress);
const token = getContract(tokenABI, tokenAddress);

export const getBalance = async (address) => {
    let balance = await contract.methods.getbalanceOf(address).call();
    return balance;
}

export const getAllowance = async (address) => {
    let allowance = await token.methods.allowance(address, contractAddress).call();
    return allowance;
}

export const getUsercode = async (address) => {
    let usercode = await contract.methods.getUsercode(address).call();
    return usercode;
}

export const isWhitelisted = async (address) => {
    let flag = await contract.methods.checkWhitelisted(address).call();
    return flag;
}

export const isCodeAvailable = async (code) => {
    let isAvailable = await contract.methods.isCodeAvailable(code).call();
    return isAvailable;
}

export const getUSDInToken = async () => {
    let price = await contract.methods.getTokenPrice(tokenAddress, 1).call();
    return parseFloat(price)/1000000;
}

export const getSubReferral = async (address) => {
    let subReferral = [];
    try {
        subReferral =await contract.methods.getSubReferral(address).call();
        return splitString(subReferral);
    } catch(error) {
        console.log(error);
        return null;
    }
}

export const getReferralDeadline = async () => {
    let referralDeadline =await contract.methods.referralDeadline().call();
    return referralDeadline;
}

export const mintWithoutReferrer = async (address, usercode, tokenUri) => {
    try{
        let dataABI = contract.methods.createAvatarWithoutReferrer(usercode, tokenUri).encodeABI();
        let txHash = await signTransaction(address, dataABI, 0);
        return txHash;
    } catch(err) {
        console.log(err);
        return null;
    }
}

export const mintWithReferrer = async (address, usercode, referrerCode, tokenUri) => {
    try{
        let dataABI = contract.methods.createAvatarWithReferrer(usercode, referrerCode, tokenUri).encodeABI();
        let txHash = await signTransaction(address, dataABI, 0);
        return txHash;
    } catch(err) {
        console.log(err);
        return null;
    }
}

export const approve = async (address) => {
    var price = '1000000000000000000000';
    // let amount = parseInt();
    try{
        let dataABI = token.methods.approve(contractAddress, web3.utils.toWei(price, "ether")).encodeABI();
        const transactionParameters = {
            to: tokenAddress, // Required except during contract publications.
            from: address, // must match user's active address.
            data: dataABI,
            gasLimit: "0x5208",
            chainId: 43113,
            value: 0,  // this should be hex !!!!
        };
        
        //sign the transaction
        try {
            const txHash = await window.ethereum.request({
              method: "eth_sendTransaction",
              params: [transactionParameters],
            });
            return await checkTx(txHash);
        } catch (error) {
            console.log(error);
        }
    } catch(err) {
        console.log(err);
        return null;
    }
}

// sign transaction using metamask
const signTransaction = async (address, dataABI, _etherAmount) => {

    const transactionParameters = {
        to: contractAddress, // Required except during contract publications.
        from: address, // must match user's active address.
        data: dataABI,
        gasLimit: "0x5208",
        chainId: 43113,
        value: parseInt(_etherAmount).toString(16),  // this should be hex !!!!
      };
    
    //sign the transaction
    try {
        const txHash = await window.ethereum.request({
          method: "eth_sendTransaction",
          params: [transactionParameters],
        });
        return await checkTx(txHash);
    } catch (error) {
        alert("There is an error on minting your nft card.");
        console.log(error);
        return null;
    }
}

const checkTx = async (txHash) => {
    // let result = await web3.eth.getTransaction(txHash)
    let result = null;
    while(result==null) {
        result = await web3.eth.getTransactionReceipt(txHash.toString());
    }
    console.log(result, "status");
    if (result.status) return txHash;
    else return null;
}