import { 
    mintWithoutReferrer, 
    mintWithReferrer, 
    isCodeAvailable,
    getUsercode,
    approve,
    getBalance,
    getUSDInToken,
    getAllowance
} from "utils/contract";
import { mintingPrice, mintingPriceWithRef, baseUrl } from "constants/config";
import { download } from "utils/downloader";
import { upload } from "utils/uploader";

const sleep = (milliseconds) => {
    return new Promise(resolve => setTimeout(resolve, milliseconds))
}

export const checkTokenBalance = async (walletAddress, iswhiteListed, mintPrice) => {
    if (iswhiteListed) return true;
    let balance = await getBalance(walletAddress);
    let price = await getUSDInToken();
    let usdBalance = balance * price / 10**18;
    if (usdBalance > mintPrice) return true;
    else {
        alert("You don't have enough tokens.");
        return false;
    }
}

export const checkTokenAllowance = async (walletAddress, iswhiteListed) => {
    if (iswhiteListed) return true;
    let allowance = await getAllowance(walletAddress);
    if (allowance / 10**18 > 10*10) return true;
    else {
        alert("You didn't approve tokens. Please approve tokens first");
        return false;
    }
}

export const checkMintabilityOfWallet = async (walletAddress) => {
    let usercode = await getUsercode(walletAddress);
    if(usercode!="") {
        alert("This address already has a NFT");
        return false;
    }
    return true;
}

export const validate = async (values, isWhite, setIsUserIDValid, setIsReferrerIDValid, setMintPrice) => {
    const errors = {};
    
    values.userID = values.userID.replace(/[^a-zA-Z0-9_]/g, '').toUpperCase();
    values.referrerID = values.referrerID.replace(/[^a-zA-Z0-9_]/g, '').toUpperCase();
    
    if (values.userID.length > 1 && values.userID.length < 16) {
        let isValid = await isCodeAvailable(values.userID);
        setIsUserIDValid(isValid);
        if (!isValid)
            errors.userID = "User ID is already used. Please choose another one.";
    } else {
        setIsUserIDValid(false);
        errors.userID = "User ID length should be between 2 and 15";
    }
    
    if (isWhite) return errors;
    
    if (values.referrerID.length > 1 && values.referrerID.length < 16) {
        let isValid = await isCodeAvailable(values.referrerID);
        setIsReferrerIDValid(!isValid);
        if(isValid) {
            errors.referrerID = "Referrer ID is incorrect.";
            setMintPrice(mintingPrice);
        }
        else setMintPrice(mintingPriceWithRef);
    } else {
        if (values.referrerID!="")
            errors.referrerID = "Referrer ID length should be between 2 and 15";
        if (isWhite)
            setMintPrice(mintingPrice);
    }
    return errors;
}


export const mintNFTCard = async (values, connected, walletAddress, mintPrice, isWhite, isMinting, setIsMinting, setActiveStep) => {
    if (isMinting) return;
    setIsMinting(true);
    console.log("mint is started");
    if (!connected) {
        alert("Connect wallet");
        setIsMinting(false);
        return;
    }
    
    try {
        let isValid = await checkMintabilityOfWallet(walletAddress);
        if(!isValid) {
            setIsMinting(false);
            return;
        } // check wallet has nft or not
        let isBalanceEnough = await checkTokenBalance(walletAddress, isWhite, mintPrice);
        if (!isBalanceEnough) {
            setIsMinting(false);
            return;
        } // check balance
        let isAllowEnough = await checkTokenAllowance(walletAddress, isWhite, mintPrice);
        if (!isAllowEnough) {
            setIsMinting(false);
            return;
        } // check allowance
        setActiveStep(2);

        if(isWhite) await sleep(2000);

        //  let url ={data: {IpfsHash: "await  Upload(values.userID)"}};
        let url = await  Upload(values.userID);
        console.log(url);
        if (url==null) {
            setActiveStep(1);
            setIsMinting(false);
            return;
        } // upload image to ipfs
        setActiveStep(3);

        // call mint function of smart contract
        await mintNFTOnSmartContract(values, url, walletAddress, isWhite, setActiveStep);
        
    } catch(error) {
        setActiveStep(1);
        console.log(error);
    }
    setIsMinting(false);
}

const mintNFTOnSmartContract = async (values, url, walletAddress, isWhite, setActiveStep) => {
    try{
        let data;
        if (values.referrerID.length>1 && values.referrerID.length<16 && !isWhite) {
            data = await mintWithReferrer(walletAddress, values.userID, values.referrerID, url.data.IpfsHash);
            console.log(data, "tx");
        } else {
            data = await mintWithoutReferrer(walletAddress, values.userID, url.data.IpfsHash);
            console.log(data, "tx");
        }
        if (data!==null) {
            setActiveStep(4);
            downloadConfirmAlert(url.data.IpfsHash);
        } else {
            setActiveStep(1);
        }
    } catch (error) {
        setActiveStep(1);
        alert("There is an error on minting your nft card.");
        console.log(error);
    }
}

const downloadConfirmAlert = (ipfsHash) => {
    if (window.confirm('Do you want to download minted nft image?')) {
        // Save it!
        download(baseUrl+ipfsHash);
    }
}

const Upload = async (userID) => {
    try{
        console.log("Start Uploading");
        let data = await upload("avatar-container", userID);
        return data;
    } catch(error) {
        return null;
    }
}