// @mui material components
import Card from "@mui/material/Card";

// Vision UI Dashboard React components
import VuiBox from "components/VUI/VuiBox";
import VuiTypography from "components/VUI/VuiTypography";
import Table from "components/Tables/Table";
import VuiButton from "components/VUI/VuiButton";

// Data
import referralsTableData from "./data/referralsTableData";

function ReferralTable(props) {
  const { columns, rowFunction } = referralsTableData;
  const { referrals, tokenPrice, referralDeadline, refresh } = props;
  const rows = rowFunction(referrals, tokenPrice, referralDeadline);

  return (
      <VuiBox py={3}>
        <VuiBox mb={3}>
          <Card>
            <VuiBox display="flex" justifyContent="space-between" alignItems="center">
              <VuiTypography variant="lg" color="white">
                Referrals
              </VuiTypography>
              <VuiButton variant="contained" color="info" onClick={refresh}>
                Refresh
              </VuiButton>
            </VuiBox>
            <VuiBox
              sx={{
                "& th": {
                  borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                    `${borderWidth[1]} solid ${grey[700]}`,
                },
                "& .MuiTableRow-root:not(:last-child)": {
                  "& td": {
                    borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                      `${borderWidth[1]} solid ${grey[700]}`,
                  },
                },
              }}
            >
              <Table columns={columns} rows={rows} />
            </VuiBox>
          </Card>
        </VuiBox>
      </VuiBox>
  );
}

export default ReferralTable;
